import React, { useState, useEffect } from 'react';

const ExchangeRate = ({ fromCurrency, toCurrency, amount }) => {
  const [exchangeRate, setExchangeRate] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchExchangeRate = async () => {
      if (!fromCurrency || !toCurrency || !amount) return;
      
      setLoading(true);
      setError(null);
      
      try {
        const response = await fetch(`https://api.exchangerate-api.com/v4/latest/${fromCurrency}`);
        if (!response.ok) {
          throw new Error('No se pudo obtener el tipo de cambio');
        }
        
        const data = await response.json();
        const rate = data.rates[toCurrency];
        setExchangeRate(rate * amount);
      } catch (err) {
        setError('Error al obtener el tipo de cambio. Por favor, intntelo de nuevo.');
      } finally {
        setLoading(false);
      }
    };

    fetchExchangeRate();
  }, [fromCurrency, toCurrency, amount]);

  if (loading) {
    return (
      <div className="mt-4 text-center">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-indigo-600 mx-auto"></div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="mt-4 text-red-600 text-center">
        {error}
      </div>
    );
  }

  if (exchangeRate !== null) {
    return (
      <div className="mt-4 text-center">
        <div className="text-2xl font-bold text-indigo-600">
          {amount} {fromCurrency} = {exchangeRate.toFixed(2)} {toCurrency}
        </div>
      </div>
    );
  }

  return null;
};

export default ExchangeRate;
